@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face{font-family:'Augillion';src:url(/static/media/Augillion.7a239286.otf)}.red{color:#013A20}.orange{color:#6E2F15}.yellow{color:#CB8E66}.lightgreen{color:#013A20}.green{color:#013A20}html{display:flex;justify-content:center}body{background-color:#EBECEE;color:#403f43;font-family:'IBM Plex Mono';width:calc(min(100%, 1200px))}body h1{font-family:'Augillion';font-weight:400;font-size:calc(min(max(10vw, 42px), 80px));margin:0}body h2{font-family:'Augillion';font-weight:400;font-size:calc(min(max(7vw, 40px), 50px));margin:0}body p,body ul{font-size:calc(min(max(2vw, 15px), 18px));margin:0;line-height:24px}body a{color:#403f43}body a:hover{color:#013A20}body button{font-family:'IBM Plex Mono';font-size:calc(min(max(2vw, 15px), 16px));color:#403f43;opacity:0.5;border:none;padding:0;background:none;cursor:pointer}body button:hover{color:#013A20}body .active{opacity:1;color:#013A20}body .container{padding-top:100px;padding-bottom:100px}body .icon{margin-bottom:-3px;margin-right:7px}@media (max-width: 600px){.App{padding:40px 30px}.navigation{display:inline-flex}.home .card .description{margin-top:10px}}@media (min-width: 601px){.App{padding:40px 110px}}@media (min-width: 901px){.App{padding:40px 110px}.home .card{display:flex}.home .card .description{margin-left:20px}}.navigation{font-size:calc(min(max(2vw, 13px), 18px))}.navigation .nav-link{text-decoration:none;border:none}.navigation .right{float:right}.navigation .right a{margin-left:50px}.navigation .active-nav-link{color:#013A20;text-decoration:underline;pointer-events:none}.home{margin-top:calc(min(20vw, 18vh))}.home .title{display:flex;justify-content:center;flex-flow:column;-webkit-animation:none;animation:none;margin-bottom:calc(min(20vw, 18vh) - 100px)}.home .sparkle-green{width:40px}.home .sparkle-red{width:40px;margin-top:20px;margin-left:-14px}.home .sparkle-yellow{position:absolute;width:60px;margin-top:80px}.about,.work{margin-top:calc(min(5vw, 100px));margin-bottom:calc(min(5vw, 100px))}.work .tags{display:inline-flex;padding:10px 0px 30px}.work ul{padding-left:10px}.work li{display:inline-flex;margin-right:10px}.projects{display:grid;grid-template-columns:repeat(auto-fill, minmax(300px, 1fr));grid-gap:1px}.projects .card .image-link{width:300px}.projects .card .image-link img{width:300px;height:180px}.projects .card .description{margin-top:10px}.home .card{margin:25px 0px}.home .card .image-link{width:450px}.home .card .image-link img{width:450px;height:250px}.card{align-items:center;margin:10px 0px}.card .image-link{display:flex}.card .image-link img{object-fit:cover;transition:box-shadow 0.15s;border:2px solid #403f43}.card .image-link img:hover{box-shadow:5px 5px 0px #CB8E66}

